.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.page__body {
  height: 100vh;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding-inline: 50px;
  display: flex;
}

.header__logo {
  height: 40px;
}

.nav {
  margin: 0;
}

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: inherit;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: url("link_line.1dbca468.svg");
  height: 4px;
  position: absolute;
  bottom: 7px;
  left: 0;
}

.main {
  --column-amount: 1;
  grid-template-columns: repeat(var(--column-amount), 200px);
  background-color: #fff;
  place-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-amount: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-amount: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-amount: 4;
  }
}

.card {
  box-sizing: border-box;
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__photo {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  transition: color .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
}

.card__estimate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 21px;
  display: flex;
}

.card__reviews {
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 13px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
}

.card__price-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__buy-button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.14;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 0;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 2px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
